*{font-family:"Dosis",sans-serif;font-weight:300;margin:0;padding:0;box-sizing:border-box}.mainBackground{background-color:#ebebeb}.whiteBg{background-color:#fff}.allWidth{width:100vw}.minHeight{min-height:100vh}.gridBody{display:grid;grid-template-columns:1fr;grid-template-rows:1fr;padding-top:5vh;z-index:-100;overflow-x:hidden}.gridMain{display:grid;grid-template-columns:1fr;grid-template-rows:100px 80px;-webkit-justify-content:center;justify-content:center;width:95%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;max-width:600px;margin:0 auto;z-index:100;padding:1%;overflow-x:hidden;margin-bottom:5vh}.gridMain section{height:-webkit-fit-content;height:-moz-fit-content;height:fit-content}.roundedCorner{border-radius:5px;overflow:hidden}.shadow{box-shadow:5px 10px #888}.flowScroll{overflow-y:scroll}.flowScroll::-webkit-scrollbar{background-color:#cecece;width:5px}.flowScroll::-webkit-scrollbar-thumb{background-color:#4e4e4e;width:5px}.margBot{margin-bottom:20px}button{background-color:transparent;transition:-webkit-transform .2s;transition:transform .2s;transition:transform .2s, -webkit-transform .2s}button:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}button:active{-webkit-transform:scale(0.9);transform:scale(0.9)}@media(max-width: 400px){html,body{font-style:14px}}label{margin:0}.font-white{color:#fff}
.gridMode{display:grid;grid-template-columns:auto 40px;width:100%;height:-webkit-fit-content;height:-moz-fit-content;height:fit-content;-webkit-align-items:center;align-items:center}.mainBackgroundHeader{background-color:rgba(139,214,34,0)}.big-title{font-size:3rem;font-weight:bold;letter-spacing:.8rem}
.centerText{text-align:center}.font-size-10{font-size:.6rem}.gray-color{color:gray}
.mid-img{height:50%;width:50%}.img-center{margin:0 calc(25%)}
.flex-row{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;justify-items:center}.card-wide{width:100%;border-bottom:1px solid #cacacf;height:50px;padding:10px;background-color:#fff;grid-gap:10px;gap:10px;-webkit-align-items:center;align-items:center}.font-bold{font-weight:bold}.font-big{font-size:1.2rem;transition:all .2s}.font-light{font-weight:100}.blue-checkbox{margin-bottom:0px}.blue-checkbox input{display:none}.blue-checkbox .status-switch{height:25px;width:25px;border-radius:25px;color:#fff;background:linear-gradient(135deg, #6fbff9, #9d6ef5);transition:all .5s}.blue-checkbox .status-switch .inherit-size{font-size:20px;line-height:25px}.blue-checkbox input:not(:checked)~.status-switch{border:1px solid gray;background:#fff;transition:border .2s;box-shadow:2px 2px 0 13px #fff inset}.blue-checkbox input:not(:checked)~.status-switch:hover{background:linear-gradient(135deg, #6fbff9, #9d6ef5);border:2px solid transparent;box-shadow:2px 2px 0 13px #fff inset;transition:background 0s}.full-width{width:100%}.input-no-border{border:none;outline:none;font-size:1.2rem}.input-no-border :focus{background-color:red}.clickable{cursor:pointer}.no-border{border:none}.crossout{text-decoration:line-through;color:#cacacf}
.text-center{text-align:center;margin-bottom:0px}.navbar-link{color:#000;transition:.2s}.navbar-link:hover{color:#000}.navbar-link-active{font-weight:600;text-decoration:underline}.flex-centered-gap{display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;grid-gap:20px;gap:20px;width:100%}.card-footer-justify{-webkit-justify-content:space-between;justify-content:space-between;padding:10px;-webkit-align-items:center;align-items:center}.card-footer-justify *{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.card-footer-justify p{text-align:center;margin-bottom:0px}.card-footer-justify button[disabled]{cursor:not-allowed}.card-footer-justify button[disabled]:hover{-webkit-transform:scale(1);transform:scale(1)}
.bgPicture{background-color:#fff;position:absolute;-webkit-transform:translateY(-10vh);transform:translateY(-10vh);z-index:50;height:40vh;width:100%;overflow:hidden;background:linear-gradient(274deg, #6fbff9, #822be2)}.bgImg{width:100%;object-fit:cover;mix-blend-mode:luminosity;opacity:.5}
